import Data from "@/js/core/data";

const Categories_Server = "https://api.${region}.500apps.com/support";

class Categories extends Data {
  constructor() {
    super(Categories_Server);
    this.disablePCors();
  }

  async get_categories() {
    let url = `${Categories_Server}/kb/${window.domain}/${window.user}/kb_categories`;
    this.url = eval("`" + url + "`");
    return await super.get();
  }
  async get_articles_count() {
    let url = `${Categories_Server}/articles/${window.user}/${window.domain}`;
    this.url = eval("`" + url + "`");
    return await super.get();
  }
}
export default new Categories();
