<template>
  <b-row class="mt-7">
    <b-col sm="12" md="4" lg="3" class="mb-4 px-0" v-for="category in categories" :key="category.id">
      <h3 class="border-bottom d-inline-block pb-2 mb-2">{{ category.name.trim() }}</h3>
      <Sections v-if="categories_count_data.length && category.id" :category_id="category.id" class="mt-1 mb-5" :sections_count="getCategoryCount(category.name.trim())" />
    </b-col>
  </b-row>
</template>

<script>
  import Categories from "@/js/support/cname/categories";
  import Sections from "./Sections";
  import { bus } from "@/js/core/emit-bus";

  export default {
    components: { Sections },
    data() {
      return {
        categories: "",
        categories_count_data: [],
      };
    },

    async created() {
      this.categories_count_data = await Categories.get_articles_count();
      // Get categories
      this.categories = await Categories.get_categories();

      bus.$emit("close_overlay", false);
    },
    methods: {
      getCategoryCount(category_name) {
        let category_count = this.categories_count_data.find((category) => category.category_name?.trim() == category_name);
        return category_count ? JSON.parse(category_count.data) : [];
      },
    },
  };
</script>
